@charset "UTF-8";
/* reset */
/* main variables */
body {
  margin: 0px;
  padding: 0px;
  font-family: Arial, sans-serif;
  background-color: #FFF;
  height: 100%;
  line-height: 1;
  position: relative;
  font-size: 13px !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

/*MAIN*/
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset,
form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer,
header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section, main {
  display: block; }

blockquote, q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-width: 0px;
  padding: 0px;
  margin: 0px; }

html {
  height: 100%;
  -webkit-text-size-adjust: none;
  -ms-text-size-adjust: none; }

input,
textarea {
  color: #333;
  outline: none;
  border-radius: 0;
  -moz-border-radius: 0;
  -webkit-border-radius: 0;
  -webkit-appearance: none; }

input[type="button"],
input[type="submit"],
button {
  cursor: pointer; }

td {
  margin: 0px;
  padding: 0px; }

form {
  padding: 0px;
  margin: 0px; }

a {
  color: #100da9;
  -webkit-transition: all 0.2s linear;
  -moz-transition: all 0.2s linear;
  -ms-transition: all 0.2s linear;
  -o-transition: all 0.2s linear;
  transition: all 0.2s linear;
  text-decoration: none;
  outline: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

a,
span,
div,
button {
  outline: none !important; }

input[type=submit],
input[type=button],
button {
  -webkit-appearance: none;
  outline: none; }

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

*:before,
*:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

.clearfix:after,
.wrapper:after,
.row:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden; }

@font-face {
  font-family: "Conv_Pari-Match Regular";
  src: url("../../fonts/Pari-Match Regular.eot");
  src: local("☺"), url("../../fonts/Pari-Match Regular.woff") format("woff"), url("../../fonts/Pari-Match Regular.ttf") format("truetype"), url("../../fonts/Pari-Match Regular.svg") format("svg");
  font-weight: normal;
  font-style: normal; }

/*
================================================================================
|                                     HEADER                                   |
================================================================================
*/
header {
  width: 100%;
  padding: 30px 0;
  min-width: 320px;
  z-index: 20;
  position: absolute; }

.header__wrapper {
  min-width: 320px;
  padding: 0 45px 0 70px;
  margin: 0 auto;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 5; }

@media screen and (max-width: 650px) {
  .logo {
    width: 150px; }
  .header__wrapper {
    padding: 0 10px; }
  header .project__btn {
    font-size: 13px;
    padding: 10px 25px; } }

/*
================================================================================
|                                     FOOTER                                   |
================================================================================
*/
footer {
  height: 96px;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 10;
  text-align: left;
  width: 100%;
  min-width: 320px;
  order: 3;
  -webkit-order: 3;
  position: absolute;
  bottom: 0;
  display: flex;
  align-items: center; }

@media screen and (max-width: 768px) {
  footer {
    background-color: black;
    height: 160px; } }

@media screen and (min-width: 320px) and (max-width: 540px) {
  footer {
    height: 200px; } }

.main-wrapper {
  padding: 0 0 0 0;
  min-width: 320px;
  width: 100vw;
  height: 100vh;
  position: relative;
  overflow: hidden;
  min-height: 100%;
  font-family: "Conv_Pari-Match Regular", sans-serif; }

/* titles */
a:hover {
  text-decoration: none !important; }

p {
  padding: 15px 0 15px 0;
  font-weight: 400; }

h1 {
  padding: 15px 0 15px 0;
  font-size: 140px; }

h2 {
  padding: 15px 0 15px 0;
  font-size: 70px; }

h3 {
  padding: 15px 0 15px 0;
  font-weight: 400; }

li {
  list-style-type: none; }

/* text position */
.text-left {
  text-align: left !important; }

.text-center {
  text-align: center !important; }

.text-right {
  text-align: right !important; }

.nowrap {
  white-space: nowrap !important; }

/* loader */
.loaded .main-wrapper {
  opacity: 0; }

.icon-load {
  background: url(../img/loader.gif) no-repeat left top;
  width: 40px;
  height: 40px;
  position: fixed;
  left: 50%;
  top: 50%;
  margin-left: -20px;
  margin-left: -20px;
  display: none; }

.loaded .icon-load {
  display: block; }

/*
================================================================================
|                                     CONTENT                                 |
================================================================================
*/
.fullscreen {
  position: relative;
  display: flex;
  flex-direction: column; }

.content {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  min-width: 320px;
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 10; }

.content__wrapper {
  margin-bottom: 100px; }

.content__link-item {
  font-size: 3.5vh;
  text-transform: uppercase;
  color: #ffffff;
  width: max-content; }

.project__btn {
  font-size: 16px;
  text-transform: uppercase;
  color: #000000;
  border: 6px solid #000000;
  padding: 15px 35px;
  transform: skewX(-15deg);
  width: max-content;
  background: #f8ff14;
  position: relative; }
  .project__btn .project__btn-inside {
    transform: skewX(15deg);
    transition: 0.4s; }

.project__btn:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 999;
  left: 0;
  top: 0; }

.terms-and-conditions {
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1;
  text-decoration: underline;
  text-transform: uppercase; }

.content__title {
  font-style: italic;
  text-align: center;
  transform: perspective(2000px) rotateY(45deg);
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin-left: 50px; }
  .content__title h1 {
    margin: 0;
    line-height: 1;
    padding: 15px 0 15px 0;
    font-size: 16vh;
    padding-bottom: 5px;
    text-transform: uppercase;
    color: #f8ff14;
    width: max-content; }
  .content__title h2 {
    margin: 0;
    line-height: 1;
    padding: 15px 0 15px 0;
    font-size: 8vh;
    padding-top: 0;
    text-transform: uppercase;
    color: #ffffff;
    width: max-content;
    margin-left: -60px; }

.content__links-wrapper {
  padding-top: 50px;
  display: flex;
  justify-content: center; }

.content__links {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 17px; }
  .content__links .content_btn {
    font-size: 4.5vh;
    border: 1px solid;
    padding: 1vh 3vw; }
  .content__links .content_btn-black {
    border-color: #f8ff14;
    background-color: #000000;
    color: #f8ff14;
    font-weight: 200; }
  .content__links a {
    margin-top: 15px;
    color: #ffffff; }

.sky-bg {
  width: 100%;
  height: auto;
  position: absolute;
  opacity: 0.6;
  z-index: 4;
  top: 0; }
  .sky-bg img {
    margin-left: 24vw;
    margin-top: -35vh;
    width: 100%;
    animation: waves 30s infinite linear; }

@keyframes waves {
  0% {
    transform: translate(0); }
  25% {
    transform: translate(-25%, -20%); }
  50% {
    transform: translate(-50%, -30%); }
  75% {
    transform: translate(-75%, -20%); }
  100% {
    transform: translate(0); } }

.city-bg {
  width: 100%;
  height: auto;
  position: absolute;
  z-index: 1;
  top: 0;
  display: flex;
  justify-content: flex-end; }
