  z-index: 7;
  right: -237px;
  bottom: -161px; }
  .right-man-bg img {
    height: 88vh; }

.bottom-bg {
  width: 100%;
  height: 100%;
  background-image: url(../../img/trec.png);
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  z-index: 1;
  bottom: 0%; }

