//main styles

.main-wrapper {
  padding: 0 0 0 0;
  min-width: $min-width;
  width: 100vw;
  height: 100vh;
  position: relative;
  overflow: hidden;
  min-height: 100%;
  font-family: "Conv_Pari-Match Regular", sans-serif;
}

/* titles */
a:hover {
  text-decoration: none !important;
}
p {
  padding: 15px 0 15px 0;
  font-weight: 400;
}
h1 {
  padding: 15px 0 15px 0;
  font-size: 140px;
}
h2 {
  padding: 15px 0 15px 0;
  font-size: 70px;
}
h3 {
  padding: 15px 0 15px 0;
  font-weight: 400;
}
li {
  list-style-type: none;
}
/* text position */
.text-left {
  text-align: left !important;
}
.text-center {
  text-align: center !important;
}
.text-right {
  text-align: right !important;
}
.nowrap {
  white-space: nowrap !important;
}

/* loader */
.loaded .main-wrapper {
  opacity: 0;
}
.icon-load {
  background: url(../img/loader.gif) no-repeat left top;
  width: 40px;
  height: 40px;
  position: fixed;
  left: 50%;
  top: 50%;
  margin-left: -20px;
  margin-left: -20px;
  display: none;
}
.loaded .icon-load {
  display: block;
}

/*
================================================================================
|                                     CONTENT                                 |
================================================================================
*/
.fullscreen {
  position: relative;
  display: flex;
  flex-direction: column;
}

.content {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  min-width: $min-width;
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 10;
}

.content__wrapper {
  margin-bottom: 100px;
}

.content__link-item {
  font-size: 3.5vh;
  text-transform: uppercase;
  color: #ffffff;
  width: max-content;
}

.project__btn {
  font-size: 16px;
  text-transform: uppercase;
  color: #000000;
  border: 6px solid #000000;
  padding: 15px 35px;
  transform: skewX(-15deg);
  width: max-content;
  background: #f8ff14;
  position: relative;

  .project__btn-inside {
    transform: skewX(15deg);
    transition: 0.4s;
  }
}
.project__btn:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 999;
  left: 0;
  top: 0;
}

.terms-and-conditions {
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1;
  text-decoration: underline;
  text-transform: uppercase;
}

.content__title {
  font-style: italic;
  text-align: center;
  transform: perspective(2000px) rotateY(45deg);
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin-left: 50px;

  h1 {
    margin: 0;
    line-height: 1;
    padding: 15px 0 15px 0;
    font-size: 16vh;
    padding-bottom: 5px;
    text-transform: uppercase;
    color: #f8ff14;
    width: max-content;
  }

  h2 {
    margin: 0;
    line-height: 1;
    padding: 15px 0 15px 0;
    font-size: 8vh;
    padding-top: 0;
    text-transform: uppercase;
    color: #ffffff;
    width: max-content;
    margin-left: -60px;
  }
}

.content__links-wrapper {
  padding-top: 50px;
  display: flex;
  justify-content: center;
}

.content__links {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 17px;

  .content_btn {
    font-size: 4.5vh;
    border: 1px solid;
    padding: 1vh 3vw;
  }

  .content_btn-black {
    border-color: #f8ff14;
    background-color: #000000;
    color: #f8ff14;
    font-weight: 200;
  }

  a {
    margin-top: 15px;
    color: #ffffff;
  }
}

.sky-bg {
  width: 100%;
  height: auto;
  position: absolute;
  opacity: 0.6;
  z-index: 4;
  top: 0;
  img {
    margin-left: 24vw;
    margin-top: -35vh;
    width: 100%;
    animation: waves 30s infinite linear;
  }
}

@keyframes waves {
  0% {
    transform: translate(0);
  }
  25% {
    transform: translate(-25%, -20%);
  }

  50% {
    transform: translate(-50%, -30%);
  }

  75% {
    transform: translate(-75%, -20%);
  }
  100% {
    transform: translate(0);
  }
}

.city-bg {
  width: 100%;
  height: auto;
  position: absolute;
  z-index: 1;
  top: 0;
  display: flex;
  justify-content: flex-end;
  img {
    margin-top: -17vh;
  }
}

.road-bg {
  width: 100%;
  height: auto;
  position: absolute;
  z-index: 5;
  top: 0%;

  img {
    width: 105vw;
    z-index: 1;
    margin-top: -14vh;
    margin-left: -2vw;
  }
}

.car-bg {
  position: absolute;
  z-index: 6;
  perspective: 800px;
  img {
    width: 44vw;
    margin-left: 19vw;
    margin-top: -1vh;
  }
}

.left-man-bg {
  position: absolute;
  z-index: 7;
  left: -247px;
  bottom: -103px;
  img {
    height: 85vh;
  }
}

.right-man-bg {
  position: absolute;
  z-index: 7;
  right: -237px;
  bottom: -161px;
  img {
    height: 88vh;
  }
}

.bottom-bg {
  width: 100%;
  height: 100%;
  background-image: url(../../img/trec.png);
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  z-index: 1;
  bottom: 0%;
}

