@media screen and (min-width: 767px) {
  .project__btn {
    transition: 0.4s;
    &:hover {
      transform: skewX(0deg);
      transition: 0.4s;

      .project__btn-inside {
        transform: skewX(0deg);
        transition: 0.4s;
      }
    }
  }

  .content__link-item {
    transition: 0.4s;
    &:hover {
      color: #ffffff;
      text-decoration: underline;
    }
  }

  .terms-and-conditions {
    &:hover {
      text-decoration: none;
      color: #ffffff;
    }
  }
}

