/*
================================================================================
|                                     HEADER                                   |
================================================================================
*/
header {
  width: 100%;
  padding: 30px 0;
  min-width: $min-width;
  z-index: 20;
  position: absolute;
}

.header__wrapper {
  min-width: $min-width;
  padding: 0 45px 0 70px;
  margin: 0 auto;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 5;
}

@media screen and (max-width: 650px) {
  .logo {
    width: 150px;
  }

  .header__wrapper {
    padding: 0 10px;
  }

  header {
    .project__btn {
      font-size: 13px;
      padding: 10px 25px;
    }
  }
}
