/*
================================================================================
|                                     FOOTER                                   |
================================================================================
*/
footer {
  height: 96px;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 10;
  text-align: left;
  width: 100%;
  min-width: $min-width;
  order: 3;
  -webkit-order: 3;
  position: absolute;
  bottom: 0;
  display: flex;
  align-items: center;
}

@media screen and (max-width: 768px) {
  footer {
    background-color: rgba(0, 0, 0, 1);
    height: 160px;
  }
}

@media screen and (min-width: 320px) and (max-width: 540px) {
  footer {
    height: 200px;
  }
}
